"use client";

import React from 'react';
import Link from 'next/link';
import Image from 'next/image';
import { NavigationMenu, NavigationMenuContent, NavigationMenuItem, NavigationMenuLink, NavigationMenuList, NavigationMenuTrigger } from "@/components/ui/navigation-menu";
import { categories } from '@/data/categories';

const MegaMenu = () => {
  return (
    <NavigationMenu className="max-w-full w-full bg-white">
      <NavigationMenuList className="flex-wrap">
        {categories.map((category) => (
          <NavigationMenuItem key={category.name}>
            <NavigationMenuTrigger className="text-sm">
              {category.name}
            </NavigationMenuTrigger>
            
            <NavigationMenuContent>
              <div className="grid w-[800px] gap-3 p-4 md:grid-cols-3">
                <div className="col-span-1">
                  <Link href={category.href} className="block select-none space-y-1 rounded-md p-3 leading-none no-underline outline-none transition-colors hover:bg-accent hover:text-accent-foreground focus:bg-accent focus:text-accent-foreground">
                    <div className="relative h-[140px] w-full overflow-hidden rounded-md">
                      <Image
                        src={category.image}
                        alt={category.name}
                        fill
                        className="object-cover"
                      />
                    </div>
                    <div className="mt-2 text-sm font-medium leading-none">{category.name}</div>
                  </Link>
                </div>
                
                <div className="col-span-2 grid gap-3">
                  {category.subcategories?.map((subcategory) => (
                    <div key={subcategory.name} className="space-y-2">
                      <h3 className="font-medium text-sm">{subcategory.name}</h3>
                      {subcategory.subcategories && (
                        <ul className="grid grid-cols-2 gap-2">
                          {subcategory.subcategories.map((item) => (
                            <li key={item}>
                              <Link 
                                href="#"
                                className="text-xs text-muted-foreground hover:text-primary"
                              >
                                {item}
                              </Link>
                            </li>
                          ))}
                        </ul>
                      )}
                    </div>
                  ))}
                </div>
              </div>
            </NavigationMenuContent>
          </NavigationMenuItem>
        ))}
      </NavigationMenuList>
    </NavigationMenu>
  );
};

export default MegaMenu;
